
import { getListTask,getDetailInfo} from '../../service/service.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'

export default{
data(){
return{
infoList:[],
detailInfo:{},
show:false,
bugshow:false,
storyshow:true,
show1:false,
show2:false,
datashow:true,
nextshow:false,
resultshow:false,
resolvetshow:false,
acceptshow:false,
failshow:false
}
},
components: {
Group,
Cell,
XDialog,
Icon,
CellBox,
Flow, FlowState, FlowLine
},
created(){
let token  = localStorage.getItem("token");
let code = this.$route.query.data;
let time = new Date().getTime();
getListTask(code,token).then((result)=>{
this.infoList = result;
console.log(result);

})
getDetailInfo(time,token,code).then((result)=>{
if(result.length>=1){
this.detailInfo = JSON.parse(result[0].json_data);
}
var t = JSON.parse(result[0].json_data);
console.log(t);
})
},
mounted(){
var self = this;
self.mescroll = new MeScroll("mescroll",{
up:{
isBounce:false,
use:false,
auto:false

},
down:{
use:false,
auto:false,
isLock:false
}
})




}

}




